import { Routes } from '@angular/router';
import { IndexComponent } from './Components/index/index.component';
import { ZivljenjepisComponent } from './Components/zivljenjepis/zivljenjepis.component';
import { JezikiComponent } from './Components/jeziki/jeziki.component';
import { ProjektiComponent } from './Components/projekti/projekti.component';
import { KontaktComponent } from './Components/kontakt/kontakt.component';

export const ROUTES: Routes = [
    {
      path: 'index',
      component: IndexComponent,
    },
  
    {
      path: 'zivljenjepis', 
      component: ZivljenjepisComponent,
    },
  
    {
      path: 'jeziki', 
      component: JezikiComponent,
    },
  
    {
        path: 'projekti', 
        component: ProjektiComponent,
    },
    {
        path: 'kontakt', 
        component: KontaktComponent,
    },
    {
      path: '**',
      redirectTo: 'index',
    },
  ];
  