import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'Burnik - Spletne rešitve';
  bgClass = 'default';

  constructor(
    private router: Router,
  ) {
    this.router.events.subscribe(event => {
        switch(this.router.url){
          case "/jeziki":{
            this.bgClass = "jeziki"
            break;
          }
          case "/zivljenjepis":{
            this.bgClass = "zivljenjepis";
            break;
          }
          case "/projekti":{
            this.bgClass = "projekti";
            break;
          }
          case "/kontakt":{
            this.bgClass = "kontakt";
            break;
          }
          case "/index":{
            this.bgClass = "index";
            break;
          }
          default: { 
            this.bgClass = "index";
         } 
        }
    });
  }
}
